.slide {
  width: 846px;
  min-height: 610px;
  display-flex: column;

  color: #ffffff;

  outline: 1px solid red;
}

.slide__main {
  position: relative;

  margin: 0;
  width: 100%;
  min-height: 410px;
  margin-bottom: 28px;
  padding: 60px 41px 32px 41px;
  box-sizing: border-box;

  outline: 1px solid orange;
}

.slide__main h3 {
  margin: 0;
  width: 400px;

  font-weight: 800;
  font-size: 40px;
  line-height: 50px;
  text-transform: uppercase;
  letter-spacing: 2px;

  outline: 1px solid orange;

}

.slide__main-description {
  margin: 0;
  margin-top: 17px;

  font-size: 22px;
  line-height: 30px;
  letter-spacing: 1px;

  opacity: 0.6;

  outline: 1px solid orange;
}

.slide__main-price {
  margin: 0;
  margin-top: 2px;

  font-weight: 400;
  font-size: 30px;
  line-height: 45px;

  outline: 1px solid orange;
}

.slide__main-button {
  width: 175px;
  height: 55px;
  margin-top: 23px;

  font-weight: 600;
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 3px;
  text-transform: uppercase;

  border: 1px solid orange;
}

.slide__main-image {
  position: absolute;

  top: -15px;
  right: 75px;

}

.slide__preview {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  justify-content: space-between;
  width: 100%;

  outline: 1px solid orange;
}

.slide__preview-item {
  width: 261px;
  min-height: 172px;
  padding: 22px 22px 0 22px;
  box-sizing: border-box;

  outline: 1px solid green;
}

.slide__preview-item h4 {
  width: 150px;
  margin: 0;

  font-weight: 600;
  font-size: 20px;
  line-height: 26px;
  color: green;
  text-transform: uppercase;

  outline: 1px solid orange;
}

.slide__preview-description {
  width: 125px;
  margin: 0;
  margin-top: 7px;
  margin-left: 2px;

  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.5px;
  color: orange;

  opacity: 0.6;

  outline: 1px solid orange;
}
